@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

:root {
  background-color: #222;
  color: white;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
  box-sizing: border-box;

  --input-bg-color: #1814;
  --button-bg-color: #1818;
  --accent-color: #2f2c;
  --dark-color: #181c;
  --text-hint-color: #fff4;
}

h1, h2, h3 {
  text-transform: uppercase;
  color: #2f2c;
  color: var(--accent-color);
}

button, .btn {
  background-color: #1818;
  background-color: var(--button-bg-color);
  border: 1px solid #2f2c;
  border: 1px solid var(--accent-color);
  padding: 0.5rem 1rem;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
  cursor: pointer;
  border-radius: 0;
  transition: background-color 400ms ease-in-out;
}

.btn--secondary {
  background-color: transparent;
  box-shadow: none;
  border: none;
  padding: 0.5rem 1rem;
  color: #2f2c;
  color: var(--accent-color);
  text-transform: uppercase;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
  cursor: pointer;
}

.btn--tertiary {
  background: linear-gradient( to right,transparent,#181c,transparent);
  background: linear-gradient( to right,transparent,var(--dark-color),transparent);
  background-color: transparent;
  border-top: 1px solid #2f2c;
  border-top: 1px solid var(--accent-color);
  border-left: none;
  border-bottom: 1px solid #2f2c;
  border-bottom: 1px solid var(--accent-color);
  border-right: none;
  cursor: pointer;
}

button:hover, .btn:hover {
  background-color: #181c;
  background-color: var(--dark-color);
}

input, input:focus {
  padding: 0.5rem 1rem;
  border: 1px solid #2f2c;
  border: 1px solid var(--accent-color);
  background-color: #1814;
  background-color: var(--input-bg-color);
  color: white;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
}

::-webkit-input-placeholder { 
  color: white;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
  opacity: 0.4;
}

:-ms-input-placeholder { 
  color: white;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
  opacity: 0.4;
}

::placeholder { 
  color: white;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 0.1rem;
  opacity: 0.4;
}

.container {
  padding: 2rem;
}

.mt {
  margin-top: 1rem;
}

.mt-2 {
  margin-top: 1.5rem;
}


.mt-3 {
  margin-top: 2rem;
}

.mb {
  margin-bottom: 1rem;
}

.mb-2 {
  margin-bottom: 1.5rem;
}


.mb-3 {
  margin-bottom: 2rem;
}

.ml {
  margin-left: 1rem;
}

.ml-2 {
  margin-left: 1.5rem;
}


.ml-3 {
  margin-left: 2rem;
}

.mr {
  margin-right: 1rem;
}

.mr-2 {
  margin-right: 1.5rem;
}


.mr-3 {
  margin-right: 2rem;
}

.v-layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: auto;
}

.h-layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.flex-grow {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.justify-left {
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.justify-stretch {
  -webkit-justify-content: stretch;
          justify-content: stretch;
}

.justify-right {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.align-left {
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.align-stretch {
  -webkit-align-items: stretch;
          align-items: stretch;
}

.align-right {
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.form-group {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  margin-bottom: 1rem;
}

@media screen and (max-width: 426px) {
  .form-group {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: stretch;
            align-items: stretch;
  }
}

.form-group label {
  margin-right: 1rem;
}

.text-xl{
  font-size: 2rem;
}

.text-lg{
  font-size: 1.5rem;
}

.text-sm{
  font-size: 0.75rem;
}

.text-xs{
  font-size: 0.5rem;
}

.text-accent {
  color: #2f2c;
  color: var(--accent-color);
}

.text-hint {
  color: #fff4;
  color: var(--text-hint-color);
}

.text-bold{
  font-weight: bold;
}

.card {
  background-color: #111c;
  border-radius: 0px;
  padding: 1rem;
  border: 1px solid #2f2c;
  border: 1px solid var(--accent-color);
  min-width: 200px;
  box-shadow: inset 0px 0px 40px #0000;
  transition: box-shadow 400ms ease-in-out;
}

.card:hover {
  box-shadow: inset 0px 0px 40px #2f25;
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.ReactModal__Overlay--after-open {
  opacity: 1;
}
.ReactModal__Overlay--before-close {
  opacity: 0;
}

.ReactModal__Content {
  opacity: 0;
  -webkit-transform: translate(-50%, -100%) scale(0.9);
          transform: translate(-50%, -100%) scale(0.9);
  transition: all 300ms ease-in-out;
}
.ReactModal__Content--after-open {
  opacity: 1;
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
}
.ReactModal__Content--before-close {
  opacity: 0;
  -webkit-transform: translate(-50%, -100%) scale(0.9);
          transform: translate(-50%, -100%) scale(0.9);
}
.app {
  position: relative;
  box-sizing: border-box;
}

.app-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -2;
  background: linear-gradient(#111f, #111e,  #1118),
  url(/cod_tournament/static/media/bg3.cf674720.jpg);
  background-position: center;
  background-repeat: no-repeat no-repeat;
  background-size: cover;
}

.app-content {
  z-index: 2;
}

body::-webkit-scrollbar {
  width: 4px;
}
 
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
body::-webkit-scrollbar-thumb {
  background-color: #2f2c;
  border: 1px solid #181c;
  border-radius: 0.5em;
}

.tournament-card {
  max-width: 300px;
  cursor: pointer;
}


.tournament-list > .card{
  margin-top: 1rem;
  margin-right: 1rem;
}
.home {
  min-height: 100vh;
  padding: 1rem;
  box-sizing: border-box;
  padding: 2rem 0;
}

.start-tournament-btn {
  min-height: 50vh;
}
.tournament {
  padding: 3rem 0;
}

.match {
  width: 100%;
  box-sizing: border-box;
  cursor: pointer;
}

.match th {
  font-size: small;
  text-align: left;
  padding: 0 0.5rem 0.5rem 0;
}

.match td {
  font-size: small;
  text-align: left;
  padding: 0 0.5rem 0 0;
}

.match-detail{
  margin-top: 0;
  overflow: hidden;
  transition: all 400ms ease-in-out;
}

.match-detail.match--open{
  margin-top: 1rem;
}


.team-list > .card{
  margin-top: 1rem;
  margin-right: 1rem;
}

.achievement-list > div {
  margin: 1rem;
}
.place {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.place .box {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 1rem;
  width: 150px;
  border: 1px solid var(--accent-color);
  background-color: var(--dark-color);
}

.first-place .box {
  height: 300px;
}

.second-place .box {
  height: 200px;
}

.third-place .box {
  height: 100px;
}

.result {
  width: 100%;
  box-sizing: border-box;
}

.result table {
  max-width: 100%;
  border: 1px solid var(--accent-color);
  background-color: var(--input-bg-color);
  text-align: left;
  overflow: auto;
}

.result table td, .result table th {
  padding: 1rem;
}

.result-slider {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: stretch;
          justify-content: stretch;
  overflow: auto;
}

.dots {
  display: -webkit-flex;
  display: flex;
}

.result-slide .table-container {
  overflow: visible;
}

@media screen and (max-width: 426px) {
  .result-slider {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: stretch;
            justify-content: stretch;
  }

  .place > div:first-child {
    font-size: 1rem;
  }

  .place .box {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
    border: 1px solid var(--accent-color);
    background-color: var(--dark-color);
  }

  .result-slide .table-container {
    overflow: auto;
    width: 100vw;
  }

  .result-slide {
    margin-top: 2rem;
  }

  .dots {
    display: none;
  }
}

.result-slider::-webkit-scrollbar {
  height: 0;
}

.result-slide {
  min-width: 100%;
}

.dots > div {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 1px solid var(--accent-color);
  background-color: var(--input-bg-color);
  margin: 1rem;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: -webkit-transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
  cursor: pointer;
}

.dots > div.active, .dots > div:hover {
  -webkit-transform: scale(2);
          transform: scale(2);
}

